import React, { useCallback, useEffect, useState } from "react";
import { Link, Routes, Route, useNavigate } from "react-router-dom";

import Lotto from "./lotto/Lotto";
import MainAccount from "../Pages/MainAccount";
import MainAdditem from "./main/MainAdditem";
import AimMainPage from "../Pages/AimMainpage";
import "../assets/style/style.css";
import ChartPage from "../Pages/ChartPage";
import AimSubPage from "./aim/AimSubpage";
import axios from "axios";

const Navigation = () => {
  const [user, setUser] = useState(null);
  const path = process.env.PUBLIC_URL;
  const navigate = useNavigate();

  useEffect(() => {
    const getUserSession = async () => {
      try {
        const res = await axios.get("/login");
        setUser(res.data.user);
      } catch (err) {
        console.log(" : ", err);
      }
    };
    getUserSession();
  }, []);
  const handleLogout = useCallback(async () => {
    try {
      const res = await axios.get("/logout");
      if (res.data && res.data.user === null) {
        setUser(null);
        navigate("/");
      }
    } catch (err) {
      console.log(" : ", err);
    }
  }, [navigate]);

  return (
    <div>
      <div className="globalNav">
        <ul>
          <li>
            <Link to="/home"></Link>
          </li>
          <li>
            <Link to="/chart"></Link>
          </li>
          <li>
            <Link to="/aim"></Link>
          </li>
          <li>
            <Link to="/lotto"></Link>
          </li>
        </ul>
      </div>

      <button onClick={handleLogout} className="logoutBtn">
        <img
          src={`${path}/images/logout_FILL0_wght400_GRAD0_opsz24.svg`}
          alt="log"
        />
      </button>
    </div>
  );
};

export default Navigation;
