// src/components/SignupPage.js
import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import SignStyles from "../../assets/style/SignupPage.module.css";
import axios from "axios";

const SignupPage = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    userId: "",
    userPw: "",
  });
  const { userId, userPw } = formData;

  const handleChange = useCallback((e) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  }, []);
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const res = await axios.post("/signup", formData);

      if (res.status !== 200) {
        throw new Error(" ");
      }

      alert(" ");
      navigate("/");
    } catch (err) {
      console.log(err);
    }
    console.log("formData : ", formData);
  };

  const handleCancel = () => {
    // Navigate to the login page when cancel button is clicked
    navigate("/");
  };

  return (
    <div className="signup-container">
      <h2 className={SignStyles.sign}></h2>
      <form className="signup-form" onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="new-username"></label>
          <input
            className={SignStyles.input}
            type="text"
            id="new-username"
            name="userId"
            value={userId}
            placeholder=""
            onChange={handleChange}
          />
        </div>
        <div className="form-group">
          <label htmlFor="new-password"></label>
          <input
            className={SignStyles.input}
            type="password"
            id="new-password"
            name="userPw"
            value={userPw}
            placeholder=""
            onChange={handleChange}
          />
        </div>
        <div className="form-group">
          <button type="submit" className={SignStyles.join}>
            
          </button>
          <button
            type="button"
            onClick={handleCancel}
            className={SignStyles.cancel}>
            
          </button>
        </div>
      </form>
    </div>
  );
};

export default SignupPage;
