// src/components/LoginPage.js
import React, { useCallback, useState } from "react";
import { Link, useNavigate } from "react-router-dom";

import LoginStyles from "../../assets/style/Login.module.css";
import axios from "axios";

const LoginPage = () => {
  const navigate = useNavigate();
  const [errMsg, setErrMsg] = useState("");
  const [formData, setFormData] = useState({
    userId: "",
    userPw: "",
  });
  const { userId, userPw } = formData;

  const handleChange = useCallback((e) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  }, []);

  const handleLogin = useCallback(
    async (e) => {
      e.preventDefault();

      try {
        const res = await axios.post("/login", formData);

        if (res.data.err) {
          throw new Error(" ", res.data.err);
        }

        alert(" : ", res.data);
        navigate("/home");
      } catch (err) {
        console.log(" : ", err);

        alert("  ");
      }
      console.log(" id, pw: ", formData);
    },
    [formData, navigate]
  );

  return (
    <div className={LoginStyles.LoginContainer}>
      <h2 className={LoginStyles.my}>
        
        <br />
        <span className={LoginStyles.sub}></span>
      </h2>

      <div className="signup-link">
        <p>
          <Link to="/signup" className={LoginStyles.need}>
             ?
          </Link>
        </p>

        <form className="login-form" onSubmit={handleLogin}>
          <div className="form-group">
            <label htmlFor="username"></label>
            <input
              className={LoginStyles.input}
              type="text"
              id="username"
              name="userId"
              value={userId}
              onChange={handleChange}
              placeholder=""
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="password"></label>
            <input
              className={LoginStyles.input}
              type="password"
              id="password"
              name="userPw"
              value={userPw}
              onChange={handleChange}
              placeholder=""
              required
            />
          </div>

          <div className="form-group">
            <button type="submit" className={LoginStyles.inside}>
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default LoginPage;
